<template>

  <el-row :gutter="10">
    <div v-for="(data, index) in showNumber" :key="index">
      <el-col :span="item_span">
        <el-card :body-style="{ padding: '5px' }">
          <div  v-viewer>
            <img :src="objs[index].CDNUrl" class="image">
          </div>
          <div class="content">
            <span>{{ objs[index].copyright}}</span>
          </div>
        </el-card>
      </el-col>
    </div>
  </el-row>
</template>

<script>
  export default {
    name: "DataCard",
    props: {
      showNumber: Number,
      objs: Array,
      ot: Number
    },
    data() {
      return {

        item_span: 24 / this.showNumber,
      }
    },
    mounted() {
      console.log(this.objs)
      if (this.showNumber > this.objs.length) {
        this.showNumber = this.objs.length;
        this.item_span = 24 / this.showNumber
      }
    }

  }
</script>

<style scoped>


  .el-card {
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    margin-top: 10px;
  }

  .image {

    width: 40%;
    height: 40%;

  }

  /*div img {*/

    /*cursor: pointer;*/
    /*transition: all 0.3s;*/
  /*}*/

  /*div img:hover {*/
    /*transform: scale(2.1);*/
  /*}*/

  .content {
    padding: 14px;
    display: flex;
    flex-direction: column;
    justify-content: center;

  }
</style>



